  .back-to-top {
    cursor: pointer;

    footer &{
      margin-bottom: 10px;
      position: fixed;
      right: 20px;
      bottom: 0;
      visibility: hidden;
      opacity: 0;
      z-index: 10;
      -webkit-transition: opacity .3s 0s, visibility 0s .3s;
      -moz-transition: opacity .3s 0s, visibility 0s .3s;
      transition: opacity .3s 0s, visibility 0s .3s;

      &:hover {
        opacity: 1;
      }

      i {
        background: $footer-bg-color;
        font-size: .5rem;
        border-radius: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 15px;
        width: 40px;
        height: 40px;
        float: right;
      }

      &__is-visible, &__fade-out {
        -webkit-transition: opacity .3s 0s, visibility 0s 0s;
        -moz-transition: opacity .3s 0s, visibility 0s 0s;
        transition: opacity .3s 0s, visibility 0s 0s;
      }
      &__is-visible {
        visibility: visible;
        opacity: 1;
      }
      &__fade-out {
        opacity: .5;
      }
      &__static {
        position: static;
        margin-right: 20px;
        margin-top: -45px;
        display: inline-block;
        float: right;
      }
    }
  }


