
.article-table-content {
    overflow: auto;
    display: block;
    border: 1px solid $tertiary-bg-color;
    margin-bottom: 8px;
    width: 100%;
    table {
        width: 100%;
        border-spacing: 0;
        border-collapse: collapse;
    }
    thead {
        background-color: #eee;
    }
    th, td {
        padding: 14px;
    }
    th {
        border: 1px solid $tertiary-bg-color;
    }
    th:first-child {
        border-left: none;
    }
    th:last-child {
        border-right: none;
    }

    .tableFooter{
        margin: 0 8px;
    }
}


.article-section {

    &__inline-figure {
        margin: 65px 0;
        figure {
            margin: 0;
            width: 80%;
            float: right;
            &:hover{
                cursor: default;
            }
        }
        &:after {
            display: block;
            content: '';
            clear: both;
        }
        img {
            margin-bottom: 25px;
            cursor: pointer;
        }
        figcaption {
            font-size: 0.875rem;
            line-height: 26px;
            cursor: default;
            strong {
                display: block;
                font-size: 1.125rem;
                color: $primary-bg-color;
            }
        }
    }
    &__table-footnotes {
        padding: 14px;
        ul {
            margin: 0;
            padding: 0;
            list-style: none;
        }
    }
}



