.multiIpInfo {
  font-size: 12px;
  margin-bottom: 30px;

  .ip {color: $primary-link-color;}
}

.lockss_form {
  input {margin-bottom: 0}
  fieldset {margin: 10px 0;}
  [type=submit] {width: auto;}
  .note {
    clear: both;
    font-size: 12px;
    color: $tertiary-darken-color;
    margin-top: 5px;
  }

  .msg {
    margin-bottom: 10px;
  }

  [type="submit"] {
    font-size: 15px;
    font-size: 0.9em;
    display: inline-block;
    width: auto;

    background: transparent;
    color: inherit;
  }
}