.exam-credit-claimed {
    padding: 16px 16px 25px;
    border: 1px solid $primary-text-color;
    &__header {
        font-size: 2.75rem;
        font-weight: 600;
    }
    &__title {
        font-weight: 600;
        padding-left: 5px;
    }
    &__text {
        margin-bottom: 16px;
    }
    &__details {
        margin-bottom: 18px;
    }
    &__link {
        font-weight: 600;
        i {
            font-size: 0.65rem;
            margin-left: 6px;
            font-weight: bold;
        }
    }
    &__email {
        padding-left: 3px;
    }
}
