.dashboard-article {

    .row + .row{
        margin-top: 30px;
    }

    &__title {
        font-size: 1rem;
        line-height: 1.25rem;
        font-weight: normal;
        margin: 7px 0 0;
    }

    &__label {
        margin: 0 0 1rem;
        font-weight: bold;
        text-transform: capitalize;
    }

    &__value {

    }

    .supplemental-files {
        th, td {
            padding: 5px;
        }
        tbody {
            tr:nth-child(odd){
                background: #F2F2F2;
            }
        }
    }
}
.pbArticleView {
    .accordion {
        padding-bottom: 1rem;
        margin-bottom: 1rem;

        &__control {
            display: flex;
            align-items: center;
            padding: 16px;
            margin-bottom: 0;
            font-size: 1rem;
            font-weight: 600;
            background-color: $modal-content-bg;


            i[class*=icon-] {
                font-size: 7px;
                margin: 0 0 0 25px;
            }
        }
        &__section {
            & + .accordion__section {
                margin-top: 30px;
            }
        }

        &__content {
            padding: 0 16px 16px;
            background-color: $facet-bg-color;

            h4 {
                font-size: 1rem;
                font-weight: 600;
            }

            .dashboard-article {
                &__label {
                    color: $thead-color;
                    font-size: 1rem;
                    font-weight: 600;
                    margin: 0;
                }
            }
        }

        .row + .row {
            margin-top: 25px;
        }

        .funders{
            th,
            td {
                width: auto;
            }
            thead {
                th {
                    padding: 5px 10px 5px 0;
                    font-weight: 600;
                }
            }
            tbody {
                th,
                td {
                    font-weight :normal;
                }
                tr:last-child() {
                    th,
                    td {
                        border-bottom: none;
                    }
                }
            }
        }
    }
}