.submission-authors {
    .author {
        &-surname-td, &-name-td {
            &.to_hide {
                @include media('<tablet') {
                    display: none !important;
                }
            }
        }

        &__full-name {
            display: inline-block;
            vertical-align: super;

            > span {
                margin: 0 5px;
                color: $secondary-text-color;
                font-weight: 600;
            }
        }

        &__corresponding {
            background-color: $secondary-bg-color;
            border-left: 5px solid $primary-lighten-bg-color;
        }
    }

    .ui-sortable-handle {
        display: inline-block;
        color: $secondary-text-color;
        margin: 0 !important;
    }

}
@include media('>=desktop') {
    .authors__table{
        td,
        th {
            width: 15%;

            &:first-child,
            &:last-child{
                width: 50px;
            }
            &:nth-child(6) {
                width: auto;
            }
        }
    }
}
