.articleTitlePrice {
    &:after {
        clear: both;
        content: '';
        display: block;
    }
    > p {
        color: $primary-text-color;
        margin: 0 0 10px 0;
        max-width:70%;
    }
    .item-price {
        text-align: right;
    }
}

.boldText {
    font-weight: bold;
}

.tabPanelFooter {
    margin-bottom: 10px;
    padding-bottom: 10px;
    a ~ a {
        margin-left: 10px;
        padding-left: 10px;
        border-left: 1px solid $primary-commerce-color;
    }
}

.promoCodeBlock {
    display: inline;
    @include media('<tablet') {
        &:after {
            clear: both;
            display: block;
            content: '';
        }
    }
}

.promoCodeForm {
    @include media('>=desktop') {
        display: inline-block;
    }
    input {
        width: auto;
        margin-bottom: 0;
        ~ input {
            margin-left: 10px;
        }
    }
}

.buyItemFooter {
    padding-left: 0;
    padding-top: 30px;
    border-top: 1px solid #ccc;
    p {
        margin: 0;
    }
    span {
        font-size: $h2-font-size;
    }
    .intialPrice {
        text-align: right;
        color: $tertiary-text-color;
        font-size: $h6-font-size;
        line-height: 1;
        width: 100%;
        overflow: hidden;
    }
    .totalPrice {
        float: right;
    }
    .price {
        display: inline;
    }
}

@include media('<tablet') {
    .float-right-xs-sm {
        float: right;
    }
}

.eCommerceCheckoutBuyingItemsWidget:after {
    clear: both;
    content: '';
    display: block;
}

.promoCodeMsg {
    float: left;
    padding-top: 5px;
    span {
        font-size: $h6-font-size;
        display: block;
        max-width: 250px;
    }
}