// it effects the slide on mobile

.dropdown {
    position: relative;

    &__menu {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 1000;
        display: none;
        @extend .primary-bg-color;
        float: left;
        min-width: 160px;
        font-size: 14px;
        text-align: left;
        -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
        box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
    }
    /*
    [class*="level"] {
        top: 0;
        left: 100%;
    }*/
    //  it's effects the dropdown nav menu
    &.hover, &:hover, &:focus {
        background-color: $primary-bg-color;
        > .dropdown__menu {
            display: block;
        }
        > a {
            i[class*='_arrow'] {
                transform: rotate(180deg);
            }
        }

    }
    i[class*='_arrow'] {
        font-size: 6px;
        font-weight: 700;
        color: $text-onDark;
        line-height: 1;
        opacity: .84;
        margin-left: 5px;
        padding: 3px 0 3px 3px;
    }

    &__toggle.hover {
        + .dropdown__menu {
            display: block;
        }
    }
}

.responsive-menu{
    &-container{
        background:  $primary-bg-color;

        .left-section,.right-section,.menu-section{
            display: inline-block;
        }
        @include media('<tablet'){
            margin-right: 40px;
        }
        .institution{
            color: $white;
        }
        .dropzone{
            min-width: 50px;
        }
    }

    &-nav{
        display: none;
        border-bottom: none;
        .pb-dropzone-mode &{
            display: block;
        }

        .drawer__nav{
            width: 100%;
        }

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {//for IE11
            .icon-section_arrow_d {
                display: ruby-text;
            }
        }

        .menuOpenAndClose{
            &.js--forceClose{
                .dropdown__menu{
                    display: none;
                }
                i[class*=_arrow]{
                    transform: initial !important;
                }
            }

            &.js--opened{
                >.dropdown__menu{
                    display: block;
                }

            }
        }
    }
}
.dropdown-more.hidden{
    display: none;
}