[data-db-parent-of]:not(.sticko):not(.sticko__md):not(.js--not-parent):not(.fixed-element){
    position: relative;
}

[data-db-target-of] {
    display: none;
    position: absolute;
    text-align: left;
    box-shadow: 0 3px 7px rgba(0, 0, 0, 0.15);
    z-index: 2;

    &.js--open {
        display: block;
    }

    &.inverse {
        color: $default-bg-color;
        background-color: $primary-text-color;
        padding: 25px;
    }
}

.base {
    > [data-db-target-of] {

        @include card(25px, $default-bg-color);
        min-width: 225px;
    }

 }


.dropBlock {
    &--actions {
        position: relative;
        display: inline-block;

        .dropBlock__holder {
            right: 0;
        }
        li {
            border-bottom: 1px solid $light-grey;
            width: auto;
            min-width: 104px;
            color: black;
            background: white;
        }

        @include media('<tablet'){
            width: 100%;
            .dropBlock__holder{
                display: block;
                position: static;
                box-shadow: none;
            }
            .btn--actions{
                display: none;
            }

            li{
                border: 1px solid $light-grey;
                display: block;
            }
        }

    }
    &__link{
        position: relative;
    }
    .icon-arrow_d_n{
        position: absolute;
        right:5px;
    }
}