//@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800');

body {
    background: $default-bg-color;
    color: $primary-text-color;
    font-family: $default-font-family;
    font-size: $default-font-size;
    line-height: 1.4;
    word-wrap: break-word;
    min-height: 100vh;
}

.lock-screen, .noscroll, .modal-open {
    overflow: hidden;

    &.iOS {
        position: fixed;
    }
}

a {
    color: inherit;
    text-decoration: none;
    &:hover,
    &:focus {
        color: inherit;
        text-decoration: none;
        outline: 0;

    }
    i[class*='icon-'] {
        &:before {
            display: inline-block;
        }
    }
}

img {
    max-width: 100%;
}

.content {
    padding-top: $content-top-padding;

    @include media('<desktop') {
        padding-top: $content-top-padding/2;
    }
}

.indented {
    & > * {
        text-indent: -15px;
        padding-left: 15px;
        margin-bottom: 15px;
    }
}

.border-bottom {
    padding-bottom: 10px;
    @include border-style(0, 2px, $separators-color);
}

.border-top {
    @include border-style(2px, 0, $primary-lighten-bg-color);
    padding-top: 8px;
}

.inline-icon {
    i {
        padding-right: 5px;
        display: inline-block;
    }
}

.block-icon {
    text-align: center;
    display: block;

    ~ span {
        text-align: center;
        display: block;
    }
}

i[class*='icon-'] {
    transition: transform 0.5s;
    &.js--rotated {
        transform: rotate(180deg);
    }
}

hr {
    &.separator {
        border: 0;
        border-top: 1px solid $secondary-bg-color;
        margin-bottom: 40px;
        margin-top: 40px;
    }
    &.section__separator {
        border: 0;
        border-top: 1px solid $borders;
        margin-top: 15px;
        margin-bottom: 30px;
    }
}

.row {
    &.separator {
        margin-top: 30px;
        margin-left: 0;
        margin-right: 0;
    }
    &[class*='--extra']{
        margin-top: 55px;
        margin-left: 0;
        margin-right: 0;
    }
}

.section__separator {
    @extend .clearfix;

    border-bottom: 1px solid $borders;
    margin-top: 30px;
    margin-bottom: 30px;
}

.reset-padding.reset-padding { //to increase the priority
    padding: 0;
}

.left-side-image {

    img {
        margin-bottom: 0;
        width: 20%;
        float: left;
        padding-right: 20px;
    }
}

span.separator {
    border-left: 1px solid #212121;
    margin-left: 10px;
    padding-left: 10px;
    display: inline-block;
    height: 14px;
}

.fixed{
    position: fixed;
    //background: $white;
}

.success-message {
    font-size: 0.75rem;
    background: $success-message-bg-color;
    color: $success-message-text-color;
    padding: 10px;
    border: 1px solid $success-message-text-color;
}

/*in page builder edit mode*/

#pb-editor {
    .pb-widget-layout {
        padding: 20px 0;

        &:after, &:before {
            content: " ";
            clear: both;
            display: table;
        }
    }

    .header {
        position: static!important;
    }

    .owl-carousel {
        display: block;
    }
}

.pb-ad {
    display: none;
    height: 0;

    @include media('>=desktop') {
        display: table;
        height: auto;
        margin-left: auto;
        margin-right: auto;
    }
}

.note {
    font-size: 0.75rem;
    color: $note-text;
    margin: 0;
}

.error {
    color: red;
    margin: 0;
    font-size: 0.75rem;
}

.success {
    color: green;
    margin: 15px 0;
    font-size: 0.75rem;
}

.warning{
    color: orange;
    margin: 0;
    font-size: 0.75rem;
}

// Allows you to hide text but maintain spacing and borders.
.hidden-text {
    color: transparent;
}