.submission-list {
    &__list {
        min-height: 627px;
        max-height: 1045px;
        overflow: auto;
    }
    &-item {
        position: relative;
        min-height: 0;
        margin: 0;
        max-width: 340px;
        padding: 8px;
        background-color: #e7eff8;
        font-size: .875rem;
        border-bottom: 1px solid $primary-border-color;
        cursor: pointer;

        &:hover,
        &.selected {
            background-color: #fff;
            box-shadow: -2px 2px 0 0 rgba(0, 0, 0, 0.12);
            z-index: 2;
        }

        &__badge {
            display: flex;
            position: absolute;
            right: 8px;
            top: 9px;
        }
        &__row{
            margin-bottom: 1rem;
        }

        &__title {
            font-size: 1rem;
            line-height: 1.25rem;
            font-weight: bold;
            margin: 7px 0 0;
        }

        &__label {
            font-weight: bold;
            text-transform: capitalize;
        }

        &__value {
            color: #9e9e9e;
        }

        a{
            position: absolute;
            display: block;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
    }
}