.refineSearch {
    &__content {
        padding: 15px 15px 20px;
        background: $search-bg-color;
        max-width: 570px;
        margin: 0 auto;
        clear: both;

        @include media('<desktop') {
            padding: 15px 0;
        }

        &:after {
            content: "";
            display: block;
            clear: both;
        }
    }

    .jcf-select-opener {
        display: none;
    }

    &__top {
        margin-bottom: 36px;
        text-align: center;

        @include media('<desktop') {
            text-align: left;
        }
    }

    &__title,
    &__ctrl__history,
    &__ctrl__saved {
        display: inline-block;
    }

    &__ctrl__history,
    &__ctrl__saved {
        @include media('<desktop') {
            width: 50%;
        }
    }

    &__title {
        padding-bottom: 8px;
        border-bottom: 2px solid;
        float: left;

        @include media('<desktop') {
            float: none;
            width: 100%;
            margin-bottom: 20px;
            text-align: left;
        }
    }

    &__ctrl__saved {
        float: right;
    }

    &__texts {
        .jcf-select {
            width: 28.5%;
            float: left;

            @include media('<desktop') {
                min-width: 30%;
            }
        }
    }

    &__text {
        width: 59%;
        float: left;
        margin-right: 2%;
        margin-bottom: 10px;
        padding-top: 8px;
        padding-bottom: 7px;

        @include media('<desktop') {
            width: 58%;
        }
    }

    .jcf-select {
        margin-right: 1.5%;
        margin-bottom: 10px;
        height: 30px;
        padding: 7px 5px;
    }

    &__ctrl__add,
    &__ctrl__remove {
        width: 7%;
        float: right;
    }

    &__ctrl__add {
        i {
            font-size: 2rem;

            @include media('<desktop') {
                margin-left: -4px;
            }
        }
    }

    &__ctrl__remove {
        padding-top: 5px;

        i {
            font-size: 1.5625rem;
        }
    }

    .refineSearch__sep {
        float: left;
        margin-right: 1.5%;
        font-size: 0.75rem;
        margin-top: 5px;

        @include media('<desktop') {
            display: block;
            float: none;
            clear: both;
            margin-bottom: 5px;
        }
    }

    &__date__title {
        padding-bottom: 12px;
        border-bottom: 1px solid $primary-text-color;
        display: block;
        font-size: 0.75rem;
        margin-bottom: 20px;
        color: $tertiary-darken-color;
    }

    &__date {
        clear: both;
        margin-bottom: 6px;

        &__last {
            float: left;
        }

        .jcf-select {
            width: 28.5%;
            float: left;
        }

        .jcf-select-text {
            margin: 0;
        }

        &__custom {
            clear: left;
            float: left;
        }

        .jcf-select-refineSearch__month,
        .jcf-select-refineSearch__year {
            width: 15.5%;
            min-width: 50px;

            @include media('<desktop') {
                width: 49%;
                margin-right: 0;
            }
        }

        .jcf-select-refineSearch__month {
            margin-right: 1.5%;

            @include media('<desktop') {
                margin-right: 2%;
                clear: left;
            }
        }
    }

    &__row {
        &:last-child {
            margin-bottom: 20px;
        }
    }

    &__ctrl {
        clear: both;
        text-align: center;
    }

    .radio--primary {
        padding-top: 6px;
        margin-right: 3%;
        margin-bottom: 20px;

        span:before {
            border-width: 1px;
            background: $default-bg-color;
        }
    }

    label {
        font-weight: normal;
    }

    &__button {
        font-size: 0.75rem;
        padding: 8px 53px 9px;
        border: 0;
    }

    .dropBlock & {
        position: static;
    }
}


.refineRegion {
    position: relative;

    .refineSearch {
        position: absolute;
        top: 100%;
        z-index: 1;
        right: 0;
        left: 0;
        background: $default-bg-color;
        box-shadow: 0 15px 18px rgba($refine-search-shadow-color, 0.18);
        margin: 0 -999px;
        padding: 0 999px;
        @include media('<desktop') {
            padding-top: 15px;
            padding-bottom: 15px;
        }
    }

    .refineSearch__content {
        background: transparent;
    }
}