.js--sticko {
  position: fixed;
  z-index: 2;
  background: #fff; }
  .js--sticko.js--open {
    z-index: 3; }

@media (max-width: 991px) {
  .sticko--md.js--sticko {
    position: relative; } }

.sticko.slide {
  transform: translateX(-100vw);
  -webkit-transform: translateX(-100vw);
  -moz-transform: translateX(-100vw);
  -ms-transform: translateX(-100vw);
  -o-transform: translateX(-100vw); }

.scrollThenFix {
  position: relative;
  top: auto; }
  .scrollThenFix.locked {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    margin-right: 0;
    margin-left: 0;
    background-color: #fff;
    z-index: 100; }

.default-bg-color {
  background: #fff; }

.primary-bg-color, .main-nav, .dropdown__menu {
  background: #212121; }

.primary-lighten-bg-color {
  background: #555; }

.secondary-bg-color {
  background: #eee; }

.secondary-lighten-bg-color {
  background: #f9f9f9; }

.tertiary-lighten-bg-color {
  background: #f5f5f5; }

.tertiary-bg-color {
  background: #b0b0b0; }

.primary-text-color {
  color: #000; }

.primary-darken-text-color {
  color: #555; }

.secondary-text-color {
  color: #777; }

.tertiary-text-color {
  color: #d6d6d6; }

.tertiary-darken-color {
  color: #b8b8b8; }

.text-onLight {
  color: #000; }

.text-onDark {
  color: #fff; }
  .text-onDark h1, .text-onDark h2, .text-onDark h3, .text-onDark h4, .text-onDark h5, .text-onDark h6 {
    color: #fff; }

body {
  background: #fff;
  color: #000;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  line-height: 1.4;
  word-wrap: break-word;
  min-height: 100vh; }

.lock-screen, .noscroll, .modal-open {
  overflow: hidden; }
  .lock-screen.iOS, .noscroll.iOS, .modal-open.iOS {
    position: fixed; }

a {
  color: inherit;
  text-decoration: none; }
  a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    outline: 0; }
  a i[class*='icon-']:before {
    display: inline-block; }

img {
  max-width: 100%; }

.content {
  padding-top: 121px; }
  @media (max-width: 991px) {
    .content {
      padding-top: 60.5px; } }

.indented > * {
  text-indent: -15px;
  padding-left: 15px;
  margin-bottom: 15px; }

.border-bottom {
  padding-bottom: 10px;
  border-width: 0;
  border-style: solid;
  border-color: #f0f0f0;
  border-top-width: 0;
  border-bottom-width: 2px; }

.border-top {
  border-width: 0;
  border-style: solid;
  border-color: #555;
  border-top-width: 2px;
  border-bottom-width: 0;
  padding-top: 8px; }

.inline-icon i {
  padding-right: 5px;
  display: inline-block; }

.block-icon {
  text-align: center;
  display: block; }
  .block-icon ~ span {
    text-align: center;
    display: block; }

i[class*='icon-'] {
  transition: transform 0.5s; }
  i[class*='icon-'].js--rotated {
    transform: rotate(180deg); }

hr.separator {
  border: 0;
  border-top: 1px solid #eee;
  margin-bottom: 40px;
  margin-top: 40px; }

hr.section__separator {
  border: 0;
  border-top: 1px solid #e1e1e1;
  margin-top: 15px;
  margin-bottom: 30px; }

.row.separator {
  margin-top: 30px;
  margin-left: 0;
  margin-right: 0; }

.row[class*='--extra'] {
  margin-top: 55px;
  margin-left: 0;
  margin-right: 0; }

.section__separator {
  border-bottom: 1px solid #e1e1e1;
  margin-top: 30px;
  margin-bottom: 30px; }

.reset-padding.reset-padding {
  padding: 0; }

.left-side-image img {
  margin-bottom: 0;
  width: 20%;
  float: left;
  padding-right: 20px; }

span.separator {
  border-left: 1px solid #212121;
  margin-left: 10px;
  padding-left: 10px;
  display: inline-block;
  height: 14px; }

.fixed {
  position: fixed; }

.success-message {
  font-size: 0.75rem;
  background: #eee;
  color: #04a361;
  padding: 10px;
  border: 1px solid #04a361; }

/*in page builder edit mode*/
#pb-editor .pb-widget-layout {
  padding: 20px 0; }
  #pb-editor .pb-widget-layout:after, #pb-editor .pb-widget-layout:before {
    content: " ";
    clear: both;
    display: table; }

#pb-editor .header {
