.administratorsWidget {
    button[type="submit"] {
        font-size: 15px;
        display: inline-block;
        width: auto;
        background: transparent;
        color: $profile-primary-color;
    }

    .instructions {
        font-size: 10px;
    }

    form {
        label {
            margin: 12px 0;
        }
    }

    input:not(:last-child) {
        margin: 0;
    }

    input.btn--inverse {
        width: auto;
        font-size: 15px;
        color: $profile-primary-color;
    }

    .email-addresses {
        margin-left: 15px;
        font-weight: bold;
        color: $profile-primary-color;
        font-size: 15px;

        &:hover {
            color: $primary-text-color;
        }
    }
}

#DeleteInstitutionAdmin_AdminManager {
    table {
        margin-bottom: 0.5em;

        td, th {
            padding: 0.5em;
        }

        .table__mark {
            width: 20px;
        }
    }

    label {
        margin: 0;
    }
}

#institutionUsageReport {
    word-break: break-word;
}