.alert {

    &s {
        .intro-text {
            font-size: 0.75rem;
            color: $account-color;
        }

        h5 {
            color: $profile-primary-color;
            margin-top: 80px;
        }

        .btn--inverse {
           font-size: 0.75rem;
        }

        [class*='btn'] {
            font-weight: bold;
        }

        p.inline {
            display: inline-block;
            margin: 0;
            margin-right: 3px;
        }

        .section__separator label {margin-bottom: 0;}

    }

    &__freq {
        .jcf-select, .left-side-label label, .input-group {
            @include media('>=tablet') {
                width: 50%;
            }

            width: 100%;
        }
    }

    &__format {
        @extend .clearfix;

        .left-side-label {

            .input-group {
                width: 50%;
                @include media('>=tablet') {
                    float: right;
                }

                text-align: right;
            }
        }

        .radio--primary {
            text-align: left;
            color: $primary-light-text-color;
            font-size: 1rem;
            font-weight: normal;
            width: auto;

            &:first-child {
                margin-right: 50px;
            }

            span:before {
                width: 20px;
                height: 20px;
            }

            input:checked~span:before {
                box-shadow: inset 0px 0px 0px 3px rgba(255,255,255,1);
            }
        }
    }

    &__list {
        .serialTitle {
            display: block;
        }

        .parent-item, label, td {
            color: $profile-primary-color;

        }

        label {
            font-size: 1rem;
            font-weight: normal;
        }

        td label {
            font-size: 0.875rem;
            font-weight: bold;
        }

        td {
            font-size: 0.75rem;
            font-weight: bold;
        }

        .input-group span {
            display: inline-block;
            padding-left: 30px;

            &:before {
                position: absolute;
                left: 0;
                top: 3px;
            }
        }

        @include media('<tablet') {
            &.expandable {
                td:not(:first-child):before {
                    display: none;
                }

                td:nth-child(2) {
                    label {
                        width: 80%;
                        float: left;
                    }

                }
                .to_hide {
                    margin-left: 32px;
                }
            }
        }
    }

    &__sort {

        .jcf-select {
            width: auto;
            padding: 0;
            border:none;


        }
        .jcf-select-text {
            font-size: 0.75rem;
            font-weight: bold;
            margin: 3px 10px;
            color: $profile-tertiary-color;
        }

        .left-side-label {
            label {
                width: auto;
                color: $profile-tertiary-color;

                @include media('<tablet') {
                    display: inline-block;
                }
            }
        }
    }

    &__action > .js__citActions {
        background: transparent;
        padding: 0;
        color: $account-color;
        font-size: 0.75rem;
        padding-left: 10px;
        margin-left: 10px;
        border-left: 1px solid $primary-light-text-color;

        i {
            margin-left: 0;
            margin-right: 10px;
            font-size: 1rem;
        }
    }
    &__action.dropBlock {
        position: relative;

        > a {
            background: transparent;
            padding: 0;
            color: $account-color;
            font-size: 0.75rem;
            padding-left: 10px;
            margin-left: 10px;
            border-left: 1px solid $primary-light-text-color;

            span {
                margin-right: 10px;
            }

            i {
                 font-size: 7px;
            }
        }

        .dropBlock__holder {
            min-width: 160px;

            a {
                padding: 10px 5px;
                display: block;
            }

            i {
                margin-left: 0;
                margin-right: 10px;
                font-size: 1rem;
            }
        }

    }
}
