.pagination {
    text-align: center;
    margin-top: 100px;
    font-size: 0.875rem; // 14px

    @include media('<tablet') {
        margin-top: 50px;
    }

    &__btn--prev {
        &:before {
            font-family: 'icomoon' !important;
            speak: none;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            content: "\e624";
            font-size: 0.875rem; // 14px
            padding-right: 10px;
        }
    }
    &__btn--next {
        &:after {
            font-family: 'icomoon' !important;
            speak: none;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            content: "\e625";
            font-size: 0.875rem; // 14px
            padding-left: 10px;
        }
    }

    &__list {
        display: inline;

        a {
            padding: 0 23px;
            @include media('<phone'){
                padding: 0 13px;
            }

            &.active {
                font-weight: bold;
                text-decoration: underline;
            }

            &:hover {
                text-decoration: underline;
            }
        }
    }
}

