.ipRangesWidget {
  margin-bottom: 40px;

  .info {
    font-size: 12px;
  }

  [type="submit"] {
    font-size: 15px;
    font-size: 0.9em;
    display: inline-block;
    width: auto;
    margin: 10px;
    background: transparent;
    color: inherit;
  }

  input:not(:last-child) {
    margin-left:0 ;
  }
}

.ipexamples {
  font-size: 10px;
  padding-top: 2px;

  .example + .example:before {
    display: inline-block;
    content: 'or';
    padding: 0 5px;
  }
}

#institutionMultiIpAdd {
  margin-bottom: 20px;

  label {
    margin: 12px 0;
  }

  @include media('<tablet') {
    padding: 0 1em;
  }
}

#institutionMultiIpChange {
  @include media('<tablet') {

    padding: 0 1em;

    thead {
      display: none;
    }

    tr + tr {
      border-top: 1px solid $input-border;
    }

    td:first-of-type {
      padding-left: 0;
    }
    td:nth-of-type(2) {
      padding-top: 1em;
    }
    td:nth-of-type(3) {
      padding-bottom: 1em;
    }
    td:not(:first-of-type) {
      display: block;
      float: left;
      width: 100%;
      padding-right: 0;
    }
  }

  input:not([type="submit"]) {
    margin: 0;
  }

  table {
    margin-bottom: 0.5em;

    td,th {
      padding: 0.5em;
    }

    .error {
      max-width: 150px;
      font-size: .9375rem;
    }
  }

  label {
    margin: 0;
  }
}