.frequency-block {
    margin-top: 30px;
    margin-bottom: 30px;
}

.content-alert-form {
    h3 {
        margin-top: 40px;
    }
    li {
        margin-top: 20px;
    }
}

.address-block {
    border-width: 0;
    border-style: solid;
    border-color: $light-grey;
    border-bottom-width: 2px;
    margin-bottom: 40px;
}

.user-addresses {
    i {
        vertical-align: middle;
    }
    p {
        font-size: 13px;
    }
}

@include media('>=desktop') {
    .profile-pages {
        .tab--aside {
            > .tab__content {
                padding-left: 40px;
            }
            > .tab__nav {

                .active a {
                    color: #333;
                    border-color: #333;
                }
                a {
                    color: #9D9D9D;
                }
            }
        }
    }
}

.subscriptions-form {
    position: relative;
    .quick-search__button {
        right: 1px;
    }
}