.header {
  width: 100%;
  z-index: 10;

  &.fixed {
    background: #fff;
    position: fixed !important;
    top: 0;
  }

  &--first-row {
    padding: 15px 0;
    @include media('<desktop') {
      &.container {
        padding: 12px 50px 12px 12px;
      }
      .row {
        margin: 0;
      }
    }

    &.container {
      max-width: 1080px;
      width: 100%;
    }
  }

  .contact__nav {
    margin: 0;

    li {
      display: inline-block;
    }

    a {
      margin: 0;
    }
  }

  &.js--sticko {
    width: 100%;
  }

  @include media('<desktop') {

    .institution-wrapper {
      display: none;
    }

    .clonedDiv {
      a {
        font-weight: normal;
      }
    }

  }

  &:not(.fixed) {
    ~ .content {
      padding-top: 0;
    }
    ~ .main-container {
      .content {
        padding-top: 0;
      }
    }
  }

  //@include media('<tablet') {
  //    .pull-right, .pull-left {
  //        max-width: 50%;
  //    }
  //}
}

.header__logo {

  @include media('<desktop') {

    img {
      max-height: 30px;
    }

  }

  img {
    display: block;
  }
}

.institution-wrapper, .header__quick-menu {
  display: inline-block;
}

.header__quick-menu {
  white-space: nowrap;

  & > ul {
    display: inline-block;
  }

  > li.active {
    border-bottom: 4px solid $primary-bg-color;
  }
}

.quick-menu__item {
  text-align: center;
  padding: 0px 15px;
  display: inline-block;

  @include media('<desktop') {
    padding: 0px 10px;
  }

  & > span {
    font-size: .75rem; // 12px
    color: $primary-text-color;

    @include media('<desktop') {
      display: none;
    }

  }

  i {
    margin-bottom: 6px;
    font-size: 2rem; // 20px
  }

  .icon-search {
    font-weight: bold;
    font-size: .94em;
    margin-bottom: 8px;
  }
  .icon-person_outline {
    font-size: 1.34em;
  }
  .icon-shopping_cart {
    font-size: 1.18rem;
    margin-bottom: 7px;
  }
}

.item__removal__popup__text {
  width: 351px;
  padding: 20px;
  position: absolute;
  background-color: #fff;
  z-index: 1003;
  -webkit-box-shadow: 6px -7px 15px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 6px -7px 15px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 6px -3px 15px 0px rgba(0, 0, 0, 0.2);
  left: 59%;
  font-size: 15px;
  top: 6%;
  line-height: 20px;
  &:before {
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    bottom: 88%;
    left: 75%;
    border: 10px solid black;
    border-color: transparent transparent #fff #fff;
    transform: rotate(135deg);
    box-shadow: -3px 2px 4px 0 rgba(0, 0, 0, 0.2);
  }
  span {
    color: #333333;
  }
  a {
    color: #005dab;
  }
  .icon-exclamation {
    display: none;
  }
}

.item__removal__cart__text {
  border: 4px solid #7cba32;
  padding: 20px;
  margin-top: 20px;
  font-size: 15px;
  top: 30%;
  z-index: 1;
  width: 100%;
  margin-bottom: 30px;
  a {
    color: #005dab;
  }
  .icon-exclamation {
    color: #fff;
    background-color: #0180b3;
    width: 21px;
    height: 21px;
    display: inline-block;
    text-align: center;
    border-radius: 50%;
    vertical-align: bottom;
    &:before {
      vertical-align: sub;
    }
  }
}

@include media('<tablet') {
  .item__removal__popup__text {
    display: none;
  }
}
