.tools-box {
    border: 3px solid;
    background: $secondary-bg-color;
    padding: 24px 34px;
    display: flex;

    &__icon {
        font-size: 1.875rem; // 30px
        color: $primary-text-color;
    }

    &__content {
        flex-grow: 4;
        padding-top: 5px;
        text-transform: uppercase;

        @include media('<tablet') {
            flex-grow: 1;
        }
    }

    &__item {
        margin-right: 8px;
        padding-left: 8px;
        line-height: 1;

        @include media('<tablet') {
            margin-bottom: 10px;

            .rlist--inline.separator & {
                border-left: 0;
                display: block;
            }
        }
    }

    &__banner {
        text-align: center;
        flex-grow: 1;

        @include media('<tablet') {
            padding-top: 25px;
        }
    }

    a {
        font-weight: normal;
    }
}