.cartLabel {
    text-align: center;
    padding: 0 15px;
    display: inline-block;
    position: relative;
    .icon-shoppingcart {
        font-size: $h3-font-size;
        text-align: center;
        display: block;
        color: $primary-text-color;
        &:before {
            content: "\e95d";
        }
    }
    .cartText {
        font-size: $small-font-size;
        color: $primary-text-color;
        @include media('<desktop') {
            display: none;
        }
    }
    .shopping-cart {
        min-width: 16px;
        background-color: $primary-bg-color;
        font-size: 12px;
        line-height: 16px;
        color: white;
        border-radius: 50%;
        text-align: center;
        position: absolute;
        right: 15px;
        padding: 1px 2px;
        top: 0;
    }
}