@import "../../../../utility/grid/scss/grid-variables";

.featured {
    .grid-item{
        clear: none !important;
        &:first-child {
            width: 100%;
            .creative-work__title {
                font-size: $h2-font-size;
            }
        }
        &:nth-child(2n) {
            clear: left;
        }
    }
}

[class*=grid] {
    .section__header {
        margin-left:  floor(($grid-gutter-width / 2));
        margin-right:  floor(($grid-gutter-width / 2));
    }
}

.section__header.container {
    margin-left: auto;
    margin-right: auto;
}

.more-link {
    float: right;
    color: #777;
    font-weight: 700;
    font-size: .8125rem;
}