.read-more, .read-less {
    white-space: nowrap;
}

.removed-items-count {
    font-size: 0.675rem;
    font-weight: 600;
    color: $truncate-removed-items-text-color;
    background: $truncate-removed-items-bg-color;
    display: inline-block;
    padding: 2px 10px;
    margin: 0 5px;
    border-radius: 2px;
    white-space: nowrap;

    &:hover {
        color: $truncate-removed-items-hover-text-color;
        background: $truncate-removed-items-hover-bg-color;
    }
}
