.exam-certificate-message {
    padding: 48px;
    text-align: center;
    border: 4px solid $secondary-border-color;

    img {
        display: block;
        margin: 0 auto;
    }
    &__heading {
        font-size: 44px;
        line-height: 52px;
        padding: 40px 0;
        font-weight: 600;
    }
    &__name {
        font-size: 32px;
        line-height: 44px;
        padding: 8px 0;
    }
    &__text {
        padding: 24px 0;
    }
    &__title {
        font-weight: 600;
        line-height: 19px;
    }
    &__credit {
        padding: 24px 0;
    }
    &__points {
        font-weight: bold;
    }
    &__date {
        color: $secondary-lighten-text-color;
        font-size: 14px;
        line-height: 19px;
        padding-top: 8px;
    }
    &__link {
        padding: 16px 42px;
        font-weight: 600;
        margin: 24px 0;
    }
}