h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    margin-bottom: 15px;
}

h1, h2, h3, h4 {
    font-weight: 700;
}

h1 {
    font-size: $h1-font-size;
}

h2 {
    font-size: $h2-font-size;
}

h3 {
    font-size: $h3-font-size;
}

h4 {
    font-size: $h4-font-size;
}

h5 {
    font-size: $h5-font-size;
}

h6 {
    font-size: $h6-font-size;
}

.section__header {
    text-transform: uppercase;
    margin-bottom: 20px;
    font-size: $h5-font-size;
}

.tabs__header {
    text-transform: capitalize;
    font-weight: 700;
    font-size: $h6-font-size;
}

.page__header {
    font-size: 2rem;
    text-align: center;
    font-weight: normal;
    margin: 3rem 0;

    @include media('<phone') {
        font-size:  $h2-font-size;
    }
}
.profile__heading{
    text-transform: uppercase;
}