.citation {
    &__title {
        line-height: 1.4;
    }

    &__authors {
        margin: 0 0 15px;
    }

    &__subtitle {
        margin-bottom: 10px;
        .article--res & {
            display: none;
        }
    }

    &__breadcrumb {
        display: none;
        .article--res & {
            display: block;
            margin-top: 15px;
            font-size: 0.81rem;
            font-weight: 700;
            margin-bottom: 6px;
        }
    }
    &__access__type {
        .article--res & {
            display: none;
        }
    }
    &__top {
        margin-top: 15px;
        &__item {
            &:not(:last-child) {
                .article--res & {
                    &:after {
                        content: "|";
                        padding: 0 5px;
                        font-weight: 700;
                    }
                }
            }
        }
        i {
            color: $primary-text-color;
            font-size: 1.38rem;
            vertical-align: middle;
            display: inline-block;
        }
    }
}

