.password-less-login-container {
    max-width: 350px;
    margin: 10px auto;
    padding: 25px;
    font-family: 'Roboto', sans-serif;
    header {
        color: #212121;
        font-size: 27px;
        font-weight: 500;
        line-height: 38px;
        text-align: center;
        margin-bottom: 15px;
    }
    a {
        text-decoration: none;
        color: #0074ff;
    }

    .error {
        color: #f44336;
        font-size: 15px;
        font-weight: 400;
        line-height: 21px;
        padding: 10px 0;
        text-align: center;
    }
    .message {
        color: #000000;
        font-size: 15px;
        font-weight: 400;
        line-height: 21px;
        text-align: center;
        padding: 10px 0;
    }
    .button-container {
        display: flex;
        justify-content: space-between;
        margin: 60px 0 20px 0;
        div {
            flex-grow: 0;
            flex-basis: 100%;
            margin: 0 10px;
            &:first-child {
                margin-left: 0;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }

    input[type="text"] {
        background: #fff;
        border: none;
        border-radius: 4px;
        line-height: 36px;
        height: 36px;
        padding-left: 15px;
        color: #616161;
        font-size: 17px;
        font-weight: 400;
        -webkit-box-shadow: 0px 0px 15px 0px rgba(204, 204, 204, 1);
        -moz-box-shadow: 0px 0px 15px 0px rgba(204, 204, 204, 1);
        box-shadow: 0px 0px 15px 0px rgba(204, 204, 204, 1);
        box-sizing: border-box;
        width: 100%;
    }

    .btn {
        font-size: 15px;
        font-weight: 700;
        text-transform: uppercase;
        border-radius: 5px;
        height: 41px;
        line-height: 41px;
        text-align: center;
        width: 100%;
        display: block;
        margin-bottom: 20px;
        &.black {
            color: #fff;
            background: #000;
            border: 1px solid #656565;
        }
        &.grey {
            color: #fff;
            background: #bcbec0;
            border: 1px solid #cfd1d2;
        }

        &:disabled {
            background: #e6e6e6;
            border-color: #e6e6e6;
        }
    }

    .social-icons {
        margin-bottom: 30px;
        .icons {
            font-size: 0;
            float: right;
        }
        i {
            background: url("../img/social-icons.png") no-repeat;
            height: 24px;
            width: 24px;
            display: inline-block;
            vertical-align: top;
            margin-right: 15px;
            &.linkedin {
                background-position: 0 0;
            }
            &.twitter {
                background-position: -38px 0;
            }
            &.id {
                background-position: -38*2px 0;
            }
            &.gplus {
                background-position: -38*3px 0;
                width: 24px;
            }
            &.fb {
                background-position: -38*4px 0;
            }
        }
        a:last-child {
            i {
                margin-right: 0;
            }
        }
    }

    .checkbox {
        margin: 15px 0;
        input {
            width: 27px;
            height: 27px;
            border: 1px solid #dbdbdb;
            background: #fff;
            border-radius: 0;
            vertical-align: middle;
            margin-right: 10px;
        }
        label {
            color: #000000;
            font-size: 15px;
            font-weight: 400;
            line-height: 21px;
        }
    }
}