
.adplaceholder {
  &.edit {
    border-radius: 5px;
    border: 3px dashed #666;
    padding: 10px 5px;
    text-align: center;
    font-weight: bold;
    background: url("/specs/ux3/releasedAssets/images/widgets/ad-placeholder/widget-placeholder.png") center center no-repeat;
    background-size: 100% 100%;
  }
  &__text{
    color: #000;
  }
  &.exists{
    background: none;
    border: 1px solid #666;
    border-radius: 0;
  }
  &.debug{
    border: 1px solid #ff0000;
    padding: 10px 5px;
  }
}