[type=checkbox], [type=radio] {
    width: auto;
    margin-right: 10px;
    margin-bottom: 10px;
}

.checkbox--primary,
.radio--secondary {
    span {
        &:before {
            content: " ";
            display: inline-block;
            width: 18px;
            height: 18px;
            border: 2px solid $primary-bg-color;
            border-radius: 100%;
            margin-right: 10px;
            line-height: 15px;
            float: left;
            transition: 0.4s;
        }
    }

    input:not(.jcf) {
        &:checked ~ span {
            &:before {
                background: $primary-bg-color;
                color: #fff;
                font-size: 12px;
                text-align: center;
                content: "\e607";
                font-family: 'icomoon' !important;
                speak: none;
                font-style: normal;
                font-weight: bold;
                font-variant: normal;
                text-transform: none;
                -webkit-font-smoothing: antialiased;
            }

        }
    }
}

.radio--secondary {
    input[type='radio']:not(.jcf) {
        display: none;
    }
}

.checkbox--primary {
    input[type='checkbox']:not(.jcf) {
        display: none;
    }
}

.radio--secondary {
    input[type='text'] {
        margin-top: 10px;
    }
}

.checkbox--secondary {
    span {
        position: relative;
        display: block;
        &:before {
            content: " ";
            display: inline-block;
            width: 36px;
            height: 20px;
            background-color: $tertiary-bg-color;
            border-radius: 10px;
            line-height: 15px;
            float: right;
            position: absolute;
            right: 0;
            bottom: 0;
            transition: 0.4s;
        }
        &:after {
            position: absolute;
            content: " ";
            height: 14px;
            width: 14px;
            right: 17px;
            bottom: 3px;
            background-color: $text-onDark;
            transition: 0.4s;
            border-radius: 100%;
        }
    }
    input {
        left: 0;
        display: inline-block;
        position: absolute;
        z-index: -1;
        &:checked ~ span {
            &:before {
                background-color: $primary-bg-color;
            }
            &:after {
                right: 4px;
            }

        }
    }
}

.radio--primary {
    span {
        &:before {
            content: " ";
            display: inline-block;
            width: 15px;
            height: 15px;
            border: 2px solid $black;
            border-radius: 100%;
            margin-right: 10px;
            line-height: 15px;
            float: left;
            transition: 0.4s;
            margin-top: 3px;
        }
    }
    input {
        display: none;
        &:checked ~ span {
            &:before {
                background-color: $black;
            }

        }
    }
}

.left-side-label {
    @include media('>=phone') {
        width: 100%;

        label {
            display: inline-block;
            vertical-align: middle;
            margin: 0;
            width: 19%;

            ~ input, ~ select, ~ .ms-ctn, ~ #publicationDate, ~ textarea {
                display: inline-block;
                width: 75%;
                margin: 0 2%;
            }
        }
    }
}

.side {
    &-label {
        @include media('>=tablet') {
            display: inline-block;
            width: 20%;
            label {
                position: absolute;
                top: 10px;
            }
        }
    }
    &-content {
        @include media('>=tablet') {
            display: inline-block;
            width: 80%;
            .source_example & { //just to show correctly in source js example
                width: 75%;
            }
        }
    }
}

.account__sort {
    width: 100%;
    .left-side-label {
        label {
            width: auto;
            margin: 12px 0;
            display: inline-block;

            ~ select {
                width: 50%;
                max-width: 180px;
                margin: 0;
                margin-left: 2%;
            }
        }
        @include media('<phone') {
            label ~ select {
                float: right;
            }
        }
        @include media('>=phone') {
            text-align: right;
        }
    }
    .input-group {
        margin-bottom: 0;
    }
}

.jcf-select-inline.jcf-select-jcf {
    display: inline-block;
    width: auto;
    vertical-align: middle;
}

