label {
    display: block;
    float: none;
    font-weight: bold;
    font-size: 0.75rem;
    color: $label;
    margin-bottom: 13px;

    &.disabled {
        cursor: not-allowed !important;
        opacity: .5
    }
}

.input-group {
    margin-bottom: 25px;

    input,
    optgroup,
    select,
    textarea,
    .jcf-select,
    .jcf-file,
    .jcf-scrollbar {
        margin-bottom: 0;
    }

    span {
        line-height: 16px;
    }

    .form__links {
        margin-top: 5px;
        font-size: 0.75rem;
    }
    &.error {
        margin-bottom: 3px;

        input,
        optgroup,
        select,
        textarea,
        .jcf-select,
        .jcf-file,
        .jcf-scrollbar {
            -webkit-box-shadow: inset 0px 0px 0px 1px #EF4623;
            -moz-box-shadow: inset 0px 0px 0px 1px #EF4623;
            box-shadow: inset 0px 0px 0px 1px #EF4623;
        }

        .form__links {
            color: #EF4623;
            margin-top: 0px;
            top: -4px;
            position: relative;
        }
    }

    .label, label {
        &.error {
            color: #EF4623;

            & ~ input, & ~ select, & ~ .jcf-select{
                border-color: #EF4623;
                outline: none;
            }

            label {
                display: inline-block;
                color: #EF4623;
            }
        }
        &.info .message {
            color: #418fde;
        }
    }
}

.reproducible {
    input,
    optgroup,
    select,
    textarea {
        width: 85%;
    }

    [class*='ctrl-field'] {
        width: 5%;
        display: inline-block;
        text-align: center;
        &.hidden {
            display: none;
        }
    }

    &:nth-of-type(3) {
        .add-ctrl-field {
            display: none;
        }
    }
}

input[disabled="disabled"] {
    cursor: not-allowed !important;
    opacity: 0.5;
}

//LIT-147813
textarea.proxy_code {
    max-height: none;
    max-width: 100%;
    min-height: 300px;
}

input {
    &::-ms-clear {
        display: none;
    }
    &::-ms-reveal {
        display: none;
    }
    &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }
}

.visuallyhidden,
.is-accessible {
    left: -999em;
    position: absolute;
}

.error-input{
    border:1px solid red;

    + .jcf-select {
        border-color: red;
    }
}

