.alphanumeric-title {
    color: $facets-text;
    display: inline-block;
    margin-right: 20px;
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 10px;
    width: 22px;

    &.is--selected {
        font-weight: 700;
    }

    &.is--disabled, &:not([href]) {
        color: $facets-disabled;
    }

}


