/*
Usage:
.article {
    @include clearfix;
}
*/
@mixin clearfix {
    &:before, &:after {
        content: "";
        display: table;
    }
    &:after {
        clear: both;
    }
}

/*
Usage:
h1 {
    @include font-size(24, 16);
}
*/
@mixin font-size($size: 12, $base: 16) {
    font-size: $size + px; //fallback for old browsers
    font-size: ($size / $base) + rem;
}

/*
Usage:
h1 {
    @include align(center);
}
*/
@mixin align($align: left) {
    text-align: $align;
}

/*
Usage:
h1 {
    @include text-truncate;
}
*/
@mixin text-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

/*
Usage:
.button {
    @include float(right);
}
*/
@mixin float($float: left) {
    float: $float;
}

/*
Usage:
.article {
    @include inline-block;
}
*/
@mixin inline-block {
    display: inline-block;
}

/*
Usage:
.is-hidden {
    @include hidden;
}
*/
@mixin hidden {
    display: none;
}

/*
Usage:
.is-invisible {
    @include invisible;
}
*/
@mixin invisible {
    visibility: hidden;
}

/*
Usage:
.article {
    @include center-block(640px);
}
*/
@mixin center-block($width: null) {
    display: block;
    margin-left: auto;
    margin-right: auto;
    @if ($width) {
        width: $width;
    }
}

/* border radius mixins
Usage:
.button {
    @include border-radius(5px);
}
*/
@mixin border-radius($radius) {
    border-radius: $radius;
    background-clip: padding-box;  /* stops bg color from leaking outside the border: */
}

@mixin border-top-radius($radius) {
    border-top-right-radius: $radius;
    border-top-left-radius: $radius;
    background-clip: padding-box;
}

@mixin border-right-radius($radius) {
    border-bottom-right-radius: $radius;
    border-top-right-radius: $radius;
    background-clip: padding-box;
}

@mixin border-bottom-radius($radius) {
    border-bottom-right-radius: $radius;
    border-bottom-left-radius: $radius;
    background-clip: padding-box;
}

@mixin border-left-radius($radius) {
    border-bottom-left-radius: $radius;
    border-top-left-radius: $radius;
    background-clip: padding-box;
}

/* Cards mixin */
@mixin card($padding: 10px , $bg-color: $default-bg-color) {
    padding: $padding;
    background-color: $bg-color;
}

/* border style mixin */
@mixin border-style($border-top-width, $border-bottom-width, $color) {
    border-width: 0;
    border-style: solid;
    border-color:  $color;
    border-top-width: $border-top-width;
    border-bottom-width: $border-bottom-width;
}

// Usage:
// @include font-face(Font Family name, '#{path}/font-name', font-weight, font-style ,'Font weight name','Font Family name-no spaces');
// ex:
// @include font-face(Open Sans, '#{$openSans}/open-sans-v15-latin-300', 300, normal,'Open Sans Light','OpenSans-Light');
@mixin font-face($font-family, $file-path, $font-weight, $font-style, $local1, $local2) {
    @font-face {
        font-family: #{$font-family};
        font-weight: $font-weight;
        font-style: $font-style;
        src: url('#{$file-path}.eot');/* IE9 Compat Modes */
        src: local($local1), local($local2),
        url('#{$file-path}.eot?#iefix') format('embedded-opentype'),/* IE6-IE8 */
        url('#{$file-path}.woff2') format('woff2'), /* Super Modern Browsers */
        url('#{$file-path}.woff') format('woff'), /* Modern Browsers */
        url('#{$file-path}.ttf') format('truetype'), /* Safari, Android, iOS */
        url('#{$file-path}.svg##{$font-family}') format('svg');/* Legacy iOS */
    }
    // Chrome for Windows rendering fix: http://www.adtrak.co.uk/blog/font-face-chrome-rendering/
    @media screen and (-webkit-min-device-pixel-ratio: 0) {
        @font-face {
            font-family: #{$font-family};
            src: url('#{$file-path}.svg##{$font-family}') format('svg');
        }
    }
}