.userLoginBar {
    text-align: center;
    display: inline-block;

    &__header {
        padding: 15px;
        .close {
            margin-top: -2px;
            &:focus {
                background: transparent;
            }
        }

        h2 {
            font-size: 1.875rem;
            font-weight: 400;
        }
    }

    &__holder {
        padding: 10px;
        background: $default-bg-color;
        min-width: 200px;
    }
    &.right-side &__holder {
        left: auto !important;
        right: 0;
    }

