.content-navigation {
    margin-bottom: 20px;
    clear: both;
    i {
        font-size: 0.7rem;
    }
    &__btn--pre {
        float: left;
        width: 25%;
        i {
            margin-right: 5px;
        }
    }
    &__extra {
        float: left;
        width: 50%;
    }
    &__btn--next {
        float: right;
        width: 25%;
        text-align: right;
        i {
            margin-left: 5px;
        }
    }
}